.frame__day16 {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    box-shadow: 1px 2px 10px 0px rgba(0,0,0,0.3);
    background: #46A6DA;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow: hidden;
}

.blobb__day16 {
    position: absolute;
    width: 90px;
    height: 90px;
    top: 155px;
    left: 155px;
    background: white;
    border-radius: 50%;
    animation: blobby 5s ease-in-out infinite;
}

.blobb-ring__day16 {
    box-sizing: border-box;
    position: absolute;
    width: 110px;
    height: 110px;
    top: 145px;
    left: 145px;
    border: 3px solid white;
    border-radius: 50%;
    animation: blobby-ring 5s ease-in-out infinite;
}

.octagon__day16 {
    position: absolute;
    width: 100px;
    height: 100px;
    top: 150px;
    left: 150px;
    fill: #fff;
    animation: octa 5s ease-in-out infinite;
}

.octa-sparkles__day16 {
	position: absolute;
	width: 200px;
	height: 200px;
	top: 100px;
	left: 100px;
	animation: octa-sparkle 5s ease-in-out infinite;
}

.octa-sparkles__day16 > .sparkle__day16 {
    position: absolute;
    top: 0;
    left: 98px;
    width: 4px;
    height: 200px;
}

.octa-sparkles__day16 > .sparkle__day16:before {
    content: '';
    position: absolute;
    width: 4px;
    height: 25px;
    border-radius: 2px;
    background: white;
}

.octa-sparkles__day16 > .sparkle__day16:after {
    top: auto;
    bottom: 0;
}

.octa-sparkles__day16 > .s2__day16 {
    transform: rotate(45deg);
}

.octa-sparkles__day16 > .s3__day16 {
    transform: rotate(90deg);
}

.octa-sparkles__day16 > .s4__day16 {
    transform: rotate(135deg);
}

.triangle__day16 {
    position: absolute;
      width: 106px;
      height: 90px;
      top: 145px;
      left: 147px;
      fill: #fff;
      animation: triangle 5s ease-in-out infinite;
}

.triangle-sparkles__day16 {
	position: absolute;
	width: 200px;
	height: 200px;
	top: 100px;
	left: 100px;
	animation: triangle-sparkle 5s ease-in-out infinite;
}

.triangle-sparkles__day16 > .sparkle__day16 {
    position: absolute;
    top: 0;
    left: 98px;
    width: 4px;
    height: 200px;
}

.triangle-sparkles__day16 > .sparkle__day16:before, .sparkle__day16:after {
    display: block;
    content: '';
    position: absolute;
    width: 4px;
    height: 25px;
    top: 0;
    left: 0;
    border-radius: 2px;
    background: #fff;
}

.triangle-sparkles__day16 > .s1__day16 {
    top: 10px;
}

.triangle-sparkles__day16 > .s2__day16 {
    transform: rotate(124deg);
}

.triangle-sparkles__day16 > .s3__day16 {
    transform: rotate(237deg);
}

@keyframes blobby {
    0% {
        transform: translate3d(0, -300px, 0) scaleY(1.2);
    }
    10% {
        transform: translate3d(0, 50px, 0) scaleY(0.8);
    }
    14% {
        transform: translate3d(0, -30px, 0) scaleY(1.1);
    }
    18% {
        transform: translate3d(0, 10px, 0) scaleY(0.9);
    }
    20%, 22% {
        transform: translate3d(0, 0, 0) scaleY(1);
    }
    26% {
        transform: translate3d(0, 0, 0) scale(1.2);
    }
    32%, 74% {
        transform: translate3d(0, 0, 0) scale(0);
    }
    80% {
		transform: translate3d(0,0,0) scale(1.2);
	}
	84% {
		transform: translate3d(0,0,0) scale(0.9);
	}
	86%, 88% {
		transform: translate3d(0,0,0) scale(1);
	}
	92% {
		transform: translate3d(0,-30px,0) scaleY(0.9);
	}
	100% {
		transform: translate3d(0,300px,0) scaleY(1.2);
	}
}

@keyframes blobby-ring {
	0%, 74% {
		transform: translate3d(0,0,0) scale(0);
		opacity: 1;
	}
	80% {
		opacity: 1;
	}
	84%, 100% {
		transform: translate3d(0,0,0) scale(1.4);
		opacity: 0;
	}
}

@keyframes octa {
	0%, 30% {
		transform: translate3d(0,0,0) scale(0) rotate(22.5deg);
	}
	36% {
		transform: translate3d(0,0,0) scale(1.2) rotate(22.5deg);
	}
	40% {
		transform: translate3d(0,0,0) scale(0.9) rotate(22.5deg);
	}
	42%, 44% {
		transform: translate3d(0,0,0) scale(1) rotate(22.5deg);
	}
	48% {
		transform: translate3d(0,0,0) scale(1.2) rotate(22.5deg);
	}
	54%, 100% {
		transform: translate3d(0,0,0) scale(0) rotate(22.5deg);
	}
}

@keyframes octa-sparkle {
	0%, 30% {
		transform: translate3d(0,0,0) scale(0);
		opacity: 1;
	}
	36% {
		opacity: 1;
	}
	40%, 100% {
		transform: translate3d(0,0,0) scale(1.4);
		opacity: 0;
	}
}

@keyframes triangle {
	0%, 52% {
		transform: translate3d(0,0,0) scale(0);
	}
	58% {
		transform: translate3d(0,0,0) scale(1.2);
	}
	62% {
		transform: translate3d(0,0,0) scale(0.9);
	}
	64%, 66% {
		transform: translate3d(0,0,0) scale(1);
	}
	70% {
		transform: translate3d(0,0,0) scale(1.2);
	}
	76%, 100% {
		transform: translate3d(0,0,0) scale(0);
	}
}

@keyframes triangle-sparkle {
	0%, 52% {
		transform: translate3d(0,0,0) scale(0);
		opacity: 1;
	}
	58% {
		opacity: 1;
	}
	62%, 100% {
		transform: translate3d(0,0,0) scale(1.4);
		opacity: 0;
	}
}